<template>
    <div class="search">
        <div class="lt">
            <el-form :model="query" label-position="right" label-width="100px">
                <el-row v-for="(rowArr, rowIndex) of searchArray" :key="rowIndex" :gutter="20">
                    <el-col :span="24/rowArr.length" v-for="item of rowArr" :key="item.prop">
                        <el-form-item :label="item.label" :prop="item.prop">
                            <el-input
                                v-if="item.type === 'input'"
                                clearable
                                :placeholder="item.placeholder || inputPlaceholder"
                                v-model="query[item.prop]"
                            />
                            <el-select
                                v-else-if="item.type === 'select'"
                                clearable
                                filterable
                                :placeholder="item.placeholder || selectPlaceholder"
                                :value-key="item.optValType === 'self' ? item.valueKey : undefined"
                                v-model="query[item.prop]">
                                <el-option
                                    v-for="(optItem, optIndex) of item.options"
                                    :key="optIndex"
                                    :label="item.optLable"
                                    :value="item.optValType === 'self' ? optItem : optItem[item.optValType] " />
                            </el-select>
                            <el-select
                                v-else-if="item.type === 'remote'"
                                clearable
                                filterable
                                :filter-method="item.filterMethod"
                                :placeholder="item.placeholder || selectPlaceholder"
                                v-model="query[item.prop]">
                                <el-option
                                    v-for="(optItem, optIndex) of item.options"
                                    :key="optIndex"
                                    :label="item.optLable"
                                    :value="item.optValType === 'self' ? optItem : optItem[item.optValType] " />
                            </el-select>
                            <el-date-picker
                                v-else-if="item.type === 'daterange'"
                                type="daterange"
                                :value-format="item.valueFormat || 'yyyy-MM-dd'"
                                :unlink-panels="item.unlinkPanels"
                                clearable
                                v-model="query[item.prop]">
                            </el-date-picker>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
        </div>
        <div class="rt">
            <el-button size="small">搜索</el-button>
            <el-button size="small">重置</el-button>
        </div>
    </div>
</template>
<script>
    import Utils from '@/utils/utils.js'
    export default {
        props: {
            searchFields: {
                type: Array,
                default() {
                    return []
                }
            }
        },
        data() {
            return {
                query: this.getDefaultQuery(),
                searchArray: [],
                inputPlaceholder: '请填写',
                selectPlaceholder: '请选择',
                remotePlaceholder: '请输入关键词',
            }
        },
        created() {
            this.init()
        },
        methods: {
            chunk(sourceArr, chunk) {
                if (typeof chunk !== 'number' || isNaN(chunk)) {
                    throw new TypeError('chunk must be number type')
                }
                if (chunk === 0) {
                    return sourceArr
                }
                const array = []
                for (let i = 0; i < sourceArr.length; i++) {
                    let isEnd = i%chunk === 0
                    let _index = Math.floor(i/chunk)
                    if (isEnd) {
                        array[_index] = []
                    }
                    array[_index].push(sourceArr[i])
                }
                return array
            },
            init() {
                this.searchArray = this.chunk(this.searchFields, 2)
            },
            getDefaultQuery() {
                const fields = this.searchFields.map(item => {
                    return {
                        key: item.prop,
                        default: item.default
                    }
                })
                const obj = {}
                fields.map(item => {
                    if (Utils.isObject(item)) {
                        let value = (Utils.isObject(item.default) || Utils.isArray(item.default))
                                        ? Utils.cloneDeep(item.default)
                                        : Utils.isFunction(item.default) ? item.default() : item.default
                        obj[item.key] = value
                    } else {
                        obj[item] = undefined
                    }
                })
                return obj
            }
        }
    }
</script>
<style lang="scss" scoped>
    .search {
        width: 100%;
        clear: both;
    }
    .lt {
        float: left;
        width: calc(100% - 200px);
    }
    .rt {
        float: right;
        width: 200px;
    }
</style>
