<template>
    <div>
        <el-table
            border
            :data="list"
            :row-key="rowKey"
            style="width: 100%">
            <el-table-column v-if="showActionCol" :label="actionLabel" align="center">
                <template slot-scope="scope">
                    <Column
                        :index="scope.$index"
                        :row="scope.row"
                        :col="scope.column"
                        :render="actionRender"/>
                </template>
            </el-table-column>
            <template v-for="(item) of fields" >
                <el-table-column
                    v-if="!item.render"
                    :key="item.key"
                    :label="item.name"
                    :prop="item.key"
                    :align="item.align || 'center'">
                </el-table-column>
                <el-table-column
                    v-else
                    :key="item.key"
                    :prop="item.key"
                    :label="item.name"
                    :align="item.align || 'center'">
                    <template slot-scope="scope">
                        <Column
                            :index="scope.$index"
                            :row="scope.row"
                            :col="scope.column"
                            :render="item.render"/>
                    </template>
                </el-table-column>
            </template>
        </el-table>
    </div>
</template>
<script>
    import Column from '@/components/table-column'
    export default {
        components: {
            Column
        },
        props: {
            list: {
                type: Array,
                default: function() {
                    return []
                }
            },
            fields: {
                type: Array,
                default: function() {
                    return []
                }
            },
            rowKey: String,
            showActionCol: {
                type: Boolean,
                default: true
            },
            actionRender: {
                type: Function,
                default: () => {}
            },
            actionLabel: {
                type: String,
                default: '操作'
            }
        },
        created() {

        }
    }
</script>
