<template>
    <el-pagination
        @size-change="sizeChange"
        @current-change="currentChange"
        :current-page="pageIndex"
        :page-sizes="pageSizes"
        :page-size="pageSize"
        layout="total, sizes, prev, jumper, next"
        :total="total">
    </el-pagination>
</template>

<script>
    export default {
        props: {
            pageSizes: {
                type: Array,
                default() {
                    return [20, 40, 60, 100]
                }
            },
            pageSize: {
                type: Number,
                default: 20
            },
            total: {
                type: Number,
                default: 0
            },
            pageIndex: {
                type: Number,
                default: 1
            },
            pageSizeChange: Function,
            pageIndexChange: Function,
        },
        methods: {
            sizeChange(size) {
                this.pageSizeChange && this.pageSizeChange(size)
            },
            currentChange(pageIndex) {
                this.pageIndexChange && this.pageIndexChange(pageIndex)
            }
        }
    }
</script>

