<template>
    <div>
        <basic-search
            :search-fields="searchFields"
        />
        <basic-list
            :fields="fields"
            :list="tableData"
            :action-render="actionRender"
        />
        <basic-pagination
            :page-size="page.pageSize"
            :page-index="page.pageIndex"
            :page-size-change="pageSizeChange"
            :page-index-change="pageIndexChange"
            :total="page.total"
        />
    </div>
</template>

<script>
    import PageBase from '@/mixins/pageBase'
    import BasicSearch from '@/components/basic-search'
    import BasicList from '@/components/basic-list'
    import BasicPagination from '@/components/basic-pagination'
    export default {
        mixins: [PageBase],
        components: {
            BasicList,
            BasicPagination,
            BasicSearch,
        },
        data() {
            return {
                tableData: [],
                fields: [
                    { key: 'name', name: '姓名' },
                    { key: 'date', name: '出生日期' },
                    { key: 'email', name: '邮箱' },
                    { key: 'ip', name: 'IP地址' },
                ],
                page: {
                    pageIndex: 1,
                    pageSize: 20,
                    total: 0,
                },
                searchFields: [
                    {
                        prop: 'name',
                        label: '姓名',
                        type: 'input'
                    },
                    {
                        prop: 'date',
                        label: '出生日期',
                        type: 'daterange',
                        unlinkPanels: true
                    },
                    {
                        prop: 'ip',
                        label: 'IP地址',
                        type: 'input'
                    }
                ]
            }
        },
        created() {
            this.queryList()
        },
        methods: {
            queryList() {
                const params = {
                    page: {
                        pageSize: this.page.pageSize,
                        pageIndex: this.page.pageIndex
                    }
                }
                api.mockDemo.list(params).then(res => {
                    if (res.code !== 200) {
                        this.$message.error(res.msg)
                    } else {
                        this.tableData = res.data.rows
                        this.page.total = res.data.total
                    }
                })
            },
            actionRender(h, {row, index, col}) {
                return [
                    <el-button type="text" on-click={() => this.handleView(row)}>查看详情</el-button>
                ]
            },
            handleView(row) {
                const route = this.getRouteByPath('/mockDetail')
                this.$store.commit('app/activeTab', {
                    code: route.meta.code,
                    query: {id: row.id}
                })
            },
            pageSizeChange(size) {
                this.page.pageSize = size
                this.page.pageIndex = 1
                this.queryList()
            },
            pageIndexChange(pageIndex) {
                this.page.pageIndex = pageIndex
                this.queryList()
            }
        }
    }
</script>
